import React from 'react';
import { Box, Heading, Flex, Image } from '@chakra-ui/react';

const TechStack = () => {
    const techSkills = [
        {
            name: 'JavaScript',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/512px-Unofficial_JavaScript_logo_2.svg.png',
        },
        {
            name: 'HTML',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/512px-HTML5_logo_and_wordmark.svg.png',
        },
        {
            name: 'CSS',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/3/3d/CSS.3.svg/512px-CSS.3.svg.png',
        },
        {
            name: 'React',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1200px-React-icon.svg.png',
        },
        {
            name: 'Node.js',
            imageSrc: 'https://nodejs.org/static/images/logo-light.svg',
        },
        {
            name: 'MongoDB',
            imageSrc: 'https://upload.wikimedia.org/wikipedia/commons/thumb/9/93/MongoDB_Logo.svg/512px-MongoDB_Logo.svg.png',
        },
        {
            name:'Django',
            imageSrc:'https://cdn.worldvectorlogo.com/logos/django-community.svg',
        },
        {
            name:'Python',
            imageSrc:'https://cdn.worldvectorlogo.com/logos/python-5.svg'
        },
        {
            name:'SQlite',
            imageSrc:'https://cdn.worldvectorlogo.com/logos/sqlite.svg'}
        // Add more skills here
    ];
    const tools = [
        {
            name: 'Vercel',
            imageSrc: 'https://cdn.worldvectorlogo.com/logos/vercel.svg',
        },
        {
            name: 'Netlify',
            imageSrc: 'https://cdn.worldvectorlogo.com/logos/netlify.svg',
        },
        {
            name: 'Visual Studio Code',
            imageSrc: 'https://cdn.worldvectorlogo.com/logos/visual-studio-code-1.svg',
        },
        {
            name: 'Git',
            imageSrc: 'https://cdn.worldvectorlogo.com/logos/git-icon.svg',
        },
        {
            name: 'Postman',
            imageSrc: 'https://cdn.worldvectorlogo.com/logos/postman.svg',
        },
        {
            name: 'NPM',
            imageSrc: 'https://cdn.worldvectorlogo.com/logos/npm.svg',
        },
        // Add more tools here
    ];

    return (
        <Box as="section" id="skills" className="skills section" p={{ base: 4, md: 6, lg: 8 }} marginBottom={{ base: "100px", md: "20px" }} width={{ base: "90%", md: "90%", lg: "70%" }} m="auto" >
            <Heading as="h2" size="xl" mb={4} textAlign="center" paddingBottom="20px" marginTop={{ base: "0px", md: "20px" }}>
                Tech Stack
            </Heading>
            <Box
                display="grid"
                width="100%"
                className="skills-grid"
                gridTemplateColumns={{
                    base: 'repeat(2, 1fr)',
                    md: 'repeat(4, 1fr)',
                    lg: 'repeat(5, 1fr)'
                }}
                gap={6}
                margin="auto"
            >
                {techSkills.map((skill) => (
                    <Box
                        key={skill.name}
                        p={4}
                        textAlign="center"
                        borderWidth={3}
                        borderRadius="md"
                        bg="white"
                        transition="transform 0.2s"
                        _hover={{ transform: 'scale(1.05)', zIndex: 1 }} // Set zIndex to 1 when hovering
                        h = {{
                            lg: '200px', // Large screens
                            md: '170px', // Medium screens
                            sm: '150px', // Small screens
                          }}
                        display="flex"
                        flexDirection="column"
                        justifyContent="center"
                        alignItems="center"
                        className="skills-card"
                    >
                        <Image
                            src={skill.imageSrc}
                            alt={skill.name}
                            maxW="80px"
                            mx="auto"
                            className="skills-card-img"
                        />
                        <Heading as="h2" size="md" mt={2} className="skills-card-name">
                            {skill.name}
                        </Heading>
                    </Box>
                ))}
            </Box>

            {/* Tools Section */}
            <Box marginTop="40px">
                <Heading as="h2" size="xl" mb={4} textAlign="center" paddingBottom="20px">
                    Tools
                </Heading>
                <Box
                    display="grid"
                    width="100%"
                    className="tools-grid"
                    gridTemplateColumns={{
                        base: 'repeat(2, 1fr)',
                        md: 'repeat(4, 1fr)',
                        lg: 'repeat(5, 1fr)'
                    }}
                    gap={6}
                    margin="auto"
                >
                    {tools.map((tool) => (
                        <Box
                            key={tool.name}
                            p={4}
                            textAlign="center"
                            borderWidth={3}
                            borderRadius="md"
                            bg="white"
                            transition="transform 0.2s"
                            _hover={{ transform: 'scale(1.05)', zIndex: 1 }} // Set zIndex to 1 when hovering
                            h={{base:'200px',md:'150px',lg:'170px'}}
                            display="flex"
                            flexDirection="column"
                            justifyContent="center"
                            alignItems="center"
                            className="tools-card"
                        >
                            <Image
                                src={tool.imageSrc}
                                alt={tool.name}
                                maxW="80px"
                                mx="auto"
                                className="tools-card-img"
                            />
                            <Heading as="h2" size="md" mt={2} className="tools-card-name">
                                {tool.name}
                            </Heading>
                        </Box>
                    ))}
                </Box>
            </Box>
        </Box>
    );
};

export default TechStack;





