import React from 'react';
import {
    Box,
    Link,
    Wrap,
    WrapItem,
    Menu,
    MenuButton,
    MenuList,
    MenuItem,
    useMediaQuery,
} from '@chakra-ui/react';
import myResumePDF from './resume/Deepak_Dhankhar_Resume.pdf';
const Navbar = () => {
    const [isSmallerThan768] = useMediaQuery("(max-width: 768px)");

    const handleResumeDownload = () => {
        const downloadLink = document.createElement('a');
        downloadLink.href = 'https://drive.google.com/file/d/10ldohB74r0ziXOIVxwUdSgOHkAmMsaW8/view?usp=sharing';
        downloadLink.target = '_blank';
        downloadLink.download = 'Deepak-Dhankhar-Resume.pdf';
        downloadLink.click();
        setTimeout(() => {
            window.location.href = 'https://drive.google.com/uc?export=download&id=1DfOFeURD7CgoBOgMP3l2uLD19OYEqL3Z';
        }, 0);
    };

    return (
        <Box
            as="nav"
            className="navbar"
            id="nav-menu"
            zIndex="1000"
            position="sticky"
            top="0"
            background="#1b6ec4"
            color="black"
        >
            <Wrap
                borderBottom="1px solid black"
                py={3}
                px={{ base: 3, md: 10 }}
                fontSize="18px"
                spacing={20}
                justify={isSmallerThan768 ? "flex-start" : "flex-end"}
            >
                {!isSmallerThan768 && (
                    <>
                        <WrapItem>
                            <Link href="#home" className="nav-link home" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                Home
                            </Link>
                        </WrapItem>
                        <WrapItem>
                            <Link href="#about" className="nav-link about" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                About
                            </Link>
                        </WrapItem>
                        <WrapItem>
                            <Link href="#skills" className="nav-link skills" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                Skills
                            </Link>
                        </WrapItem>
                        <WrapItem>
                            <Link href="#projects" className="nav-link projects" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                Projects
                            </Link>
                        </WrapItem>
                        <WrapItem>
                            <Link href="#contact" className="nav-link contact" color="white" _hover={{ color: 'black' }} textDecoration="none">
                                Contact
                            </Link>
                        </WrapItem>
                    </>
                )}
                <WrapItem>
                    {isSmallerThan768 ? (
                        <Menu>
                            <MenuButton as={Link} className="nav-link resume" color="white" textDecoration="none" _hover={{ color: 'white' }}>
                                Menu
                            </MenuButton>
                            <MenuList bg="#111111" color="white" placement="bottom-start">
                                {/* Mobile menu items */}
                                <MenuItem>
                                    <Link
                                        href="#home"
                                        className="nav-link home"
                                        color="black"
                                        textDecoration="none"
                                        style={{
                                            fontSize: '18px',
                                            width: '100%',
                                            padding: '10px 20px',
                                            borderRadius: '5px',
                                        }}
                                    >
                                        Home
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                    <Link
                                        href="#about"
                                        className="nav-link home"
                                        color="black"
                                        textDecoration="none"
                                        style={{
                                            fontSize: '18px',
                                            width: '100%',
                                            padding: '10px 20px',
                                            borderRadius: '5px',
                                        }}
                                    >
                                        About
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                    <Link
                                        href="#skills"
                                        className="nav-link home"
                                        color="black"
                                        textDecoration="none"
                                        style={{
                                            fontSize: '18px',
                                            width: '100%',
                                            padding: '10px 20px',
                                            borderRadius: '5px',
                                        }}
                                    >
                                        Skills
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                    <Link
                                        href="#projects"
                                        className="nav-link home"
                                        color="black"
                                        textDecoration="none"
                                        style={{
                                            fontSize: '18px',
                                            width: '100%',
                                            padding: '10px 20px',
                                            borderRadius: '5px',
                                        }}
                                    >
                                        Projects
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                    <Link
                                        href="#contact"
                                        className="nav-link home"
                                        color="black"
                                        textDecoration="none"
                                        style={{
                                            fontSize: '18px',
                                            width: '100%',
                                            padding: '10px 20px',
                                            borderRadius: '5px',
                                        }}
                                    >
                                        Contact
                                    </Link>
                                </MenuItem>
                                <MenuItem>
                                    <Link
                                        onClick={handleResumeDownload}
                                        className="nav-link resume"
                                        color="black"
                                        textDecoration="none"
                                        style={{
                                            fontSize: '18px',
                                            width: '100%',
                                            padding: '10px 20px',
                                            borderRadius: '5px',
                                        }}
                                    >
                                        Resume
                                    </Link>
                                </MenuItem>
                            </MenuList>
                        </Menu>
                    ) : (
                        <Box id="resume-button-1" className="nav-link resume"
                        onClick={() => {
                            window.open(
                              "https://drive.google.com/file/d/10ldohB74r0ziXOIVxwUdSgOHkAmMsaW8/view?usp=sharing",
                              "_blank"
                            );
                          }}>
                            <Link
                                 href={myResumePDF}
                                 target="_blank"
                                 download="Deepak-Dhankhar-Resume"
                                color="white"
                                _hover={{ color: 'black' }}
                                textDecoration="none"
                                id="resume-link-1"
                            >
                                Resume
                            </Link>
                        </Box>
                    )}
                </WrapItem>
            </Wrap>
        </Box>
    );
};

export default Navbar;
